-----------------------------------------------------------------\n.d-badge--white {\n  --badge--bgc: var(--white);\n}\n\n.d-badge--black-700 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--black-700);\n}\n\n.d-badge--purple-100 {\n  --badge--bgc: var(--purple-100);\n}\n\n.d-badge--purple-300 {\n  --badge--bgc: var(--purple-300);\n}\n\n.d-badge--purple-400 {\n  --badge--bgc: var(--purple-400);\n  --badge--fc: var(--white);\n}\n\n.d-badge--purple-500 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--purple-500);\n}\n\n.d-badge--orange-400 {\n  --badge--bgc: var(--orange-400);\n}\n\n.d-badge--pink-300 {\n  --badge--bgc: var(--pink-300);\n}\n\n.d-badge--pink-500 {\n  --badge--bgc: var(--pink-500);\n}\n\n.d-badge--pink-600 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--pink-600);\n}\n\n.d-badge--pink-700 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--pink-700);\n}\n\n.d-badge--yellow-300 {\n  --badge--bgc: var(--yellow-300);\n}\n\n.d-badge--green-400 {\n  --badge--bgc: var(--green-400);\n}\n\n.d-badge--red-500 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--red-500);\n}\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: BANNERS\n//\n//  These are the styles for banners, which are a type of notice used\n//  to alert users about important information relevant to them.\n//  For further documentation of these styles, please visit:\n//  https://dialpad.design/components/banners\n//\n//  ============================================================================\n//  $   BASE WRAPPER\n//  ----------------------------------------------------------------------------\n.d-banner {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --banner--bgc: var(--black-025);\n    --banner--fc: var(--fc-dark);\n\n    position: fixed;\n    top: 0;\n    right: 0;\n    left: 0;\n    z-index: var(--zi-navigation-fixed);\n    display: flex;\n    box-sizing: border-box;\n    width: 100%;\n    min-height: var(--su48);\n    color: var(--banner--fc);\n    font-size: var(--fs14);\n    line-height: var(--lh6);\n    background-color: var(--banner--bgc);\n    border-radius: 0;\n    box-shadow: none;\n\n    // If you want to hide and reveal the banner\n    &[aria-hidden='true'] {\n        --topbar-height: var(--sun64);\n\n        visibility: hidden;\n        opacity: 0;\n    }\n\n    &[aria-hidden='false'] {\n        visibility: visible;\n        opacity: 1;\n    }\n\n    //  If you want to put the banner above the topbar\n    &.d-banner--pinned {\n        z-index: calc(~'var(--zi-navigation-fixed) + 1');\n        transform: translate3d(0, 0, 0);\n    }\n}\n\n.d-banner--transform {\n    transform: translate3d(0, var(--topbar-height), 0);\n}\n\n//  ============================================================================\n//  $   CONTENT CONTAINER\n//  ----------------------------------------------------------------------------\n.d-banner__dialog {\n    position: relative;\n    display: flex;\n    align-items: center;\n    width: 100%;\n    max-width: 128rem;\n    min-height: 100%;\n    margin: 0 auto;\n    padding: var(--su8);\n\n    .d-notice__content {\n        flex-direction: row;\n        align-items: center;\n\n        .d-flow4();\n    }\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-banner.d-banner--important {\n    --banner--bgc: var(--black-800);\n    --banner--fc: var(--black-025);\n}\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-banner--error {\n    --banner--bgc: var(--red-100);\n    --banner--fc: var(--red-700);\n\n    &.d-banner--important {\n        --banner--bgc: var(--red-500);\n        --banner--fc: var(--white);\n    }\n}\n\n//  $$  INFO\n//  ----------------------------------------------------------------------------\n.d-banner--info {\n    --banner--bgc: var(--purple-100);\n\n    &.d-banner--important {\n        --banner--bgc: var(--purple-400);\n        --banner--fc: var(--white);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-banner--success {\n    --banner--bgc: var(--green-100);\n    --banner--fc: var(--green-700);\n\n    &.d-banner--important {\n        --banner--bgc: var(--green-300);\n        --banner--fc: var(--green-700);\n    }\n}\n\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-banner--warning {\n    --banner--bgc: var(--yellow-100);\n\n    &.d-banner--important {\n        --banner--bgc: var(--yellow-400);\n        --banner--fc: var(--fc-dark);\n    }\n}\n","//@import (reference) '../variables/spacing.less';\n//@import (reference) 'internals.less';\n\n//\n//  DIALTONE\n//  UTILITIES: SPACING\n//\n//  These are spacing utility classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilities/spacing\n//\n//  TABLE OF CONTENTS\n//   AUTO-SPACING\n//    - VERTICALLY\n//    - HORIZONTALLY\n//   MARGIN\n//    - POSITIVE MARGINS\n//       ALL DIRECTIONS\n//       TOP MARGIN\n//       RIGHT MARGIN\n//       BOTTOM MARGIN\n//       LEFT MARGIN\n//       X-AXIS MARGIN\n//       Y-AXIS MARGIN\n//    - NEGATIVE MARGINS\n//       ALL DIRECTIONS\n//       TOP MARGIN\n//       RIGHT MARGIN\n//       BOTTOM MARGIN\n//       LEFT MARGIN\n//       X-AXIS MARGIN\n//       Y-AXIS MARGIN\n//   PADDING\n//    - ALL DIRECTIONS\n//    - TOP PADDING\n//    - RIGHT PADDING\n//    - BOTTOM PADDING\n//    - LEFT PADDING\n//    - X-AXIS PADDING\n//    - Y-AXIS PADDING\n//    - RESET PADDING\n//\n//  ============================================================================\n//